import React from "react";

import GoogleLogin from "react-google-login";
import "./styles/GoogleLogin.scss";
import { withRouter } from "react-router-dom";

const Login = (props) => {
  const responseGoogle = (resp) => {
    if (!resp && !resp.accessToken) {
      console.log(resp);
      alert("login failed");
      return;
    } else {
      console.log(resp);
      var user_name = resp.profileObj.name;
      var user_email = resp.profileObj.email;

      sessionStorage.setItem("name", JSON.stringify(user_name));
      sessionStorage.setItem("email", JSON.stringify(user_email));
      console.log(props);
      props.history.push("/addmeal");
    }
  };

  return (
    <div>
      <GoogleLogin
        clientId="91358762601-288kh07vaof867r08f5emh3vnn4crik0.apps.googleusercontent.com"
        render={(renderProps) => (
          <button
            className="glogin"
            onClick={renderProps.onClick}
            // disabled={renderProps.disabled}
          >
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M6 12C6 15.3137 8.68629 18 12 18C14.6124 18 16.8349 16.3304 17.6586 14H12V10H21.8047V14H21.8C20.8734 18.5645 16.8379 22 12 22C6.47715 22 2 17.5228 2 12C2 6.47715 6.47715 2 12 2C15.445 2 18.4831 3.742 20.2815 6.39318L17.0039 8.68815C15.9296 7.06812 14.0895 6 12 6C8.68629 6 6 8.68629 6 12Z"
                fill="#d8592b"
              />
            </svg>{" "}
            Login
          </button>
        )}
        buttonText="Login"
        onSuccess={responseGoogle}
        onFailure={responseGoogle}
        // cookiePolicy={"single_host_origin"}
      />
    </div>
  );
};

export default withRouter(Login);
